.contacts {
  box-sizing: border-box;
  padding-left: 8.8%;
  padding-right: 8.8%;
  padding-top: 71px;
  padding-bottom: 37px;
  border-top: 1px solid $border-color-4;
}

.contacts h2 {
  @include h2-desktop;
  margin-bottom: 39px;
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-left: 4px;
  height: 140px;
  position: relative;
}

.contacts__list::before {
  content: "";
  position: absolute;
  left: 52.5%;
  top: 5px;
  width: 1px;
  height: 102px;
  background-color: $border-color-4;
}

.contacts__item {
  margin-bottom: 17px;
  margin-right: 27.5%;
  width: auto;
}

.contacts h3 {
  margin: 0;
  padding: 0;
  font-weight: $font-weight-bold;
  font-size: $font-size-desktop-m;
  margin-bottom: 3px;
}

.contacts__info {
  display: block;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.523;
  color: #102654;
}

.contacts__info--link:hover,
.contacts__info--link:focus {
  opacity: 0.7;
}

.contacts__info--link:active {
  opacity: 0.5;
}

@media (max-width: $tablet-full-width) {
  .contacts {
    padding-left: 17.4%;
    padding-right: 17.4%;
    padding-bottom: 37px;
  }

  .contacts h2 {
    @include h2-tablet;
    margin-bottom: 31px;
  }

  .contacts h3 {
    font-size: $font-size-mobile-l;
    margin-bottom: 5px;
  }

  .contacts__info {
    font-size: $font-size-tablet-m;
  }

  .contacts__list {
    height: 170px;
    margin-left: 0;
  }

  .contacts__list::before {
    left: 53%;
    top: 8px;
    height: 129px;
  }

  .contacts__item {
    width: auto;
    margin-right: 19%;
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-full-width) {
  .contacts {
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 69px;
    padding-bottom: 43px;
  }

  .contacts h2 {
    @include h2-mobile;
    margin-bottom: 34px;
  }

  .contacts__list {
    height: auto;
  }

  .contacts__list::before {
    display: none;
  }

  .contacts__item {
    width: 100%;
    margin-bottom: 20px;
  }

  .contacts h3 {
    margin-bottom: 5px;
  }
}
